.header-root{position:fixed;width:100%;background-color:#222;height:50px;line-height:50px;top:0;left:0;z-index:100}.header-root svg{height:32px;width:32px;color:#a9a9a9;margin:auto;position:absolute;right:0;left:0;top:0;bottom:0}.header-root svg:hover{color:#fff}.header-root .github-container{display:inline-block;position:relative;height:50px;width:32px;float:right;padding-right:50px}.header-root .github-container .github{height:32px;width:32px;position:absolute;bottom:0;top:0;left:0;right:0;margin:auto;cursor:pointer}.header-elem{color:#dcdcdc;display:inline-block;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Cantarell", "Open Sans", "Helvetica Neue", "Ubuntu", sans-serif;margin:auto;vertical-align:middle;padding-left:30px;padding-right:30px;cursor:pointer;font-weight:bold;font-size:16px}.header-elem:hover{color:#fff;background-color:#3c3c3c}



