.tabArea-root.normal{position:relative}.tabArea-root.normal .tab{height:16px;padding:5px;display:inline-block;font-family:Arial;font-size:14px;color:grey;border:1px solid lightgrey;border-top:none;border-radius:0 0 10px 10px;float:right;cursor:pointer}.tabArea-root.normal .tab.selected{font-weight:bold;background-color:white}.tabArea-root.normal .tab-select-line{width:100%;border-bottom:3px solid darkcyan;padding-top:2px}.tabArea-root.normal .tab-content{height:calc(100% - 27px);border-bottom:1px solid lightgrey}.tabArea-root.normal .tabs{height:27px;position:absolute;bottom:0;right:0;width:100%}.tabArea-root.bubble{position:relative}.tabArea-root.bubble .tab{padding:5px;display:inline-block;color:grey;border:1px solid lightgrey;border-radius:10px;cursor:pointer;margin:0 3px;-webkit-box-shadow:3px 3px 6px 0px rgba(0,0,0,0.75);-moz-box-shadow:3px 3px 6px 0px rgba(0,0,0,0.75);box-shadow:3px 3px 6px 0px rgba(0,0,0,0.75);background-color:#edeeef}@media only screen and (min-device-width: 320px) and (max-device-width: 479px){.tabArea-root.bubble .tab{height:12px;font-size:10px}}@media only screen and (min-device-width: 480px) and (max-device-width: 767px){.tabArea-root.bubble .tab{height:16px;font-size:14px}}@media only screen and (min-device-width: 768px) and (max-device-width: 1999px){.tabArea-root.bubble .tab{height:16px;font-size:14px}}.tabArea-root.bubble .tab.selected{font-weight:bold;background-color:#fff;border-color:#ff3b3f;color:#ff3b3f}.tabArea-root.bubble .tab-select-line{width:100%;border-bottom:none;padding-top:2px}.tabArea-root.bubble .tab-content{height:calc(100% - 27px)}.tabArea-root.bubble .tabs{-ms-box-orient:horizontal;display:-webkit-box;display:-moz-box;display:-ms-flexbox;display:-moz-flex;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;height:27px;width:100%}



